import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';

import { HeaderComponent } from './header/header.component';
import { BodyComponent } from './body/body.component';
import { FooterComponent } from './footer/footer.component';
import { EstacionamientoResultComponent } from './estacionamiento-result/estacionamiento-result.component';
import { MapComponent } from './map/map.component';

// Servicios 
import { EstacionamientoService } from './estacionamiento.service';

// Rutas
import { app_routing } from './app.route';

// Google Maps
import { AgmCoreModule } from '@agm/core';
import { FavoritosComponent } from './favoritos/favoritos.component';
import { EstadoReservaComponent } from './estado-reserva/estado-reserva.component';
import { LoginComponent } from './login/login.component';
import { ProfileComponent } from './profile/profile.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    BodyComponent,
    FooterComponent,
    EstacionamientoResultComponent,
    MapComponent,
    FavoritosComponent,
    EstadoReservaComponent,
    LoginComponent,
    ProfileComponent
  ],
  imports: [
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyCsSVsdtqBmkQwJXD3dx-hg-7bQ9xrAEpE',
      libraries: ['places']
    }),
    BrowserModule,
    HttpModule,
    HttpClientModule,
    app_routing,
    ReactiveFormsModule,
    FormsModule
  ],
  providers: [EstacionamientoService],
  bootstrap: [AppComponent]
})
export class AppModule { }



