import { RouterModule, Routes } from '@angular/router';
import { EstacionamientoResultComponent } from './estacionamiento-result/estacionamiento-result.component';
import { MapComponent } from './map/map.component';
import { FavoritosComponent } from './favoritos/favoritos.component';
import { EstadoReservaComponent } from './estado-reserva/estado-reserva.component';
import { ProfileComponent } from './profile/profile.component';

 const APP_ROUTES: Routes = [
	{ path: 'estacionamientos/:termino', component: EstacionamientoResultComponent},
	{ path: 'map', component: MapComponent},
	{ path: 'favoritos', component: FavoritosComponent },
	{ path: 'rdoReserva', component: EstadoReservaComponent },
	{ path: 'profile', component: ProfileComponent },
	{ path: '**', pathMatch: 'full', redirectTo: '/'}
] ;

export const app_routing = RouterModule.forRoot(APP_ROUTES, { useHash:true });

