import { Component, OnInit } from '@angular/core';
import { EstacionamientoService } from '../estacionamiento.service';
import { Router, NavigationExtras } from '@angular/router';

@Component({
	selector: 'app-favoritos',
	templateUrl: './favoritos.component.html',
	styleUrls: ['./favoritos.component.css']
})
export class FavoritosComponent implements OnInit {

	estacionamientos:any[];


	constructor(private router: Router,
		private _estacionamientoService: EstacionamientoService
		) { }

	ngOnInit() {

		this.estacionamientos = JSON.parse(sessionStorage.getItem("fav"));

	}

	reservar(id:number){

		this._estacionamientoService.reservar(id)
		.then(data=>{

			if (data.estado === 'ok'){

				if(sessionStorage.getItem("fav") == null){
					let fav : any[] = [];
					this._estacionamientoService.getEstacionamientoId(id)
					.then( data => {
						fav.push(data);
						sessionStorage.setItem("fav",JSON.stringify(fav));
					});
				}
				else{

					let fav = JSON.parse(sessionStorage.getItem("fav"));
					let rdo = false;
					for (var i = 0; i < fav.length; ++i) {
						if (fav[i].id == id)
							rdo = true;
					}

					if ( rdo == false){

						this._estacionamientoService.getEstacionamientoId(id)
						.then( data => {
							fav.push(data);
							sessionStorage.setItem("fav",JSON.stringify(fav));
						});

					}

				}

				let navigationExtras: NavigationExtras = { queryParams: { "resultado": "ok" } };
				this.router.navigate(['rdoReserva'],navigationExtras );

			}
			else{
				let navigationExtras: NavigationExtras = { queryParams: { "resultado": "fail" } };
				this.router.navigate(['rdoReserva'],navigationExtras );        
			}



		});

	}

	cancelar(id:number){

		this._estacionamientoService.cancelParking(id)
		.then(data=>{

			if (data.estado === 'ok'){

					let fav = JSON.parse(sessionStorage.getItem("fav"));
					for (var i = 0; i < fav.length; ++i) {
						if (fav[i].id == id){
							fav.splice(i,1);
							sessionStorage.setItem("fav",JSON.stringify(fav));
						}
					}

				this.router.navigate(['map']);
			}


		});

	}



}



