import { Component, OnInit,ElementRef, NgZone, ViewChild  } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MapsAPILoader } from '@agm/core';
import {} from 'googlemaps';
import { EstacionamientoService, Estacionamiento } from '../estacionamiento.service';
import { ActivatedRoute, Router, NavigationExtras } from '@angular/router';


@Component({
	selector: 'app-map',
	templateUrl: './map.component.html',
	styleUrls: ['./map.component.css']
})
export class MapComponent implements OnInit {

	public searchControl : FormControl;


	title:string = "Trapit";
	lat: number;
	lng: number; 
	zoom: number = 14;

	estacionamientos:any[];

	constructor( private mapsAPILoader: MapsAPILoader,
		private _estacionamientoService:EstacionamientoService,
		private activatedRoute: ActivatedRoute,
		private router:Router,
		private ngZone: NgZone) { }

	ngOnInit() {
		this.searchControl = new FormControl();
		if (navigator.geolocation) {
			navigator.geolocation.getCurrentPosition(position=>{
				this.lat = position.coords.latitude;
				this.lng = position.coords.longitude;
			})

		}


		this._estacionamientoService.getEstDisp()
		.then(data=>{
			this.estacionamientos = data;

		});

		// Recibir como parametro un objeto de otro componente
		// this.activatedRoute.queryParams.subscribe( params => {
			// 		console.log("Estacionamientos pasados por parametros");
			// 		console.log(params['estacionamientos']);

			// });


		}

		reservar(id:number){

			this._estacionamientoService.reservar(id)
			.then(data=>{

				if (data.estado === 'ok'){
					console.log("ok");

					if(sessionStorage.getItem("fav") == null){
						let fav : any[] = [];
						this._estacionamientoService.getEstacionamientoId(id)
						.then( data => {
							fav.push(data);
							sessionStorage.setItem("fav",JSON.stringify(fav));
						});
					}
					else{

						let fav = JSON.parse(sessionStorage.getItem("fav"));
						let rdo = false;
						for (var i = 0; i < fav.length; ++i) {
							if (fav[i].id == id)
								rdo = true;
						}

						if ( rdo == false){

							this._estacionamientoService.getEstacionamientoId(id)
							.then( data => {
								fav.push(data);
								sessionStorage.setItem("fav",JSON.stringify(fav));
							});

						}

					}

					let navigationExtras: NavigationExtras = { queryParams: { "resultado": "ok" } };
					this.router.navigate(['rdoReserva'],navigationExtras );

				}
				else{
					let navigationExtras: NavigationExtras = { queryParams: { "resultado": "fail" } };
					this.router.navigate(['rdoReserva'],navigationExtras );        
				}

			});


		}
	}



